import React from 'react';
import calendar, { getMonthFirstDay, thisMonthDates, getMonthDays, getDateISO } from '../helpers/calendar';
import { WEEK_DAYS } from '../helpers/calendar';
export default class Month extends React.Component {
  constructor(props) {
    super(props);
    this.renderDay = this.renderDay.bind(this);
    this.renderWeeks = this.renderWeeks.bind(this);
    this.state = {
      selected: null,
    };
  }
  renderIndent() {
    var indents = [];
    for (var i = 0; i < this.props.daysToIndent; i++) {
      indents.push(<div className='indent' key={i}>0</div>);
    }
    return (
      { indents }
    );
  }
  handleSetDay(day){
    console.log(`The day: ${day} has been picked!`);
    this.setState({ selected: day});
  }

  renderDay(day, index) {
    const thisDay = new Date(day);
    const isoDate = getDateISO(thisDay);
    if (day[0] === 'indent' || day[0] === 'pad') {
      return (
        <td key={index} id={index} className="day day__blank"></td>
      );
    } else {
      return (
        <td key={getDateISO(thisDay)} className="day">
          <button id={getDateISO(thisDay)} className={`day__button${this.state.selected == isoDate ? ' day__button__selected' : ''}`} onClick={(e) => { this.handleSetDay(e.target.id)}}>
            {thisDay.getDate()}
          </button>
        </td>
      );
    }
  }
  renderWeeks(week, weekIndex) {
    return (
      <tr key={weekIndex}>
        {week.map(this.renderDay)}
      </tr>
    );
  }
  renderWeekDays(name, index) {
    return (
      <th key={index} className="month__weekday">
        {name}
      </th>
    );
  }
  goMonthBack() {

  }
  goMonthForward() {

  }

  render() {
    return (
      <div className="month">
        <table className="month__table">
          <thead>
            <tr>
              {WEEK_DAYS.map(this.renderWeekDays)}
            </tr>
          </thead>
          <tbody>
            {thisMonthDates().map(this.renderWeeks)}
          </tbody>
        </table>
      </div>
    );
  }
}


