import React from 'react';
import calendar, { THIS_YEAR, THIS_MONTH, MONTHS } from '../helpers/calendar';
import Month from './Month';

export default class Calendar extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      today: null,
      from: null,
      to: null,
    };
  }
  componentDidMount() {
    const today = new Date().setHours(0,0,0,0);
    this.setState({ today });
  }
  render() {
    return (
      <div className="calendar">
        <div className="calendar__header">
          <button className="calendar__arrow calendar__arrow__left" onClick={this.goMonthBack}>
          </button>
          <div>{`${MONTHS[THIS_MONTH - 1]} ${THIS_YEAR}`}</div>
          <button className="calendar__arrow calendar__arrow__right" onClick={this.goMonthForward}>-</button>
        </div>
        <Month
          today={this.state.today}
          from={this.state.from}
          to={this.state.to}
        />
      </div>
    );
  }
}



